"use client";

import React, { useState } from 'react';

interface CategoryItem {
    id: string;
    name: string;
    icon: string;
    color: string;
    bgColor: string;
    borderColor: string;
}

interface Folder {
    id: string;
    name: string;
    icon: string;
    color: string;
    count: number;
    monthly: string;
    items: CategoryItem[];
}

export default function Categories() {
    const [selectedTab, setSelectedTab] = useState<'Gastos' | 'Ingresos'>('Gastos');
    const [selectedItems, setSelectedItems] = useState<string[]>(['restaurantes', 'supermercado']);

    // Modal State
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [newCatName, setNewCatName] = useState('');
    const [selectedColor, setSelectedColor] = useState('#307de8');
    const [selectedIcon, setSelectedIcon] = useState('shopping_cart');
    const [selectedFolder, setSelectedFolder] = useState('ocio');

    const folders: Folder[] = [
        {
            id: 'vivienda',
            name: 'Vivienda',
            icon: 'cottage',
            color: 'blue',
            count: 3,
            monthly: '$1,200',
            items: [
                { id: 'alquiler', name: 'Alquiler', icon: 'key', color: 'text-blue-500', bgColor: 'bg-blue-50', borderColor: 'border-blue-100' },
                { id: 'agua-luz', name: 'Agua & Luz', icon: 'water_drop', color: 'text-cyan-500', bgColor: 'bg-cyan-50', borderColor: 'border-cyan-100' },
                { id: 'internet', name: 'Internet', icon: 'wifi', color: 'text-amber-500', bgColor: 'bg-amber-50', borderColor: 'border-amber-100' },
            ]
        },
        {
            id: 'alimentacion',
            name: 'Alimentación',
            icon: 'restaurant',
            color: 'emerald',
            count: 4,
            monthly: '$450',
            items: [
                { id: 'supermercado', name: 'Supermercado', icon: 'shopping_cart', color: 'text-green-500', bgColor: 'bg-green-50', borderColor: 'border-green-100' },
                { id: 'restaurantes', name: 'Restaurantes', icon: 'local_pizza', color: 'text-orange-500', bgColor: 'bg-orange-50', borderColor: 'border-orange-100' },
            ]
        },
        {
            id: 'transporte',
            name: 'Transporte',
            icon: 'directions_car',
            color: 'indigo',
            count: 2,
            monthly: '$200',
            items: [
                { id: 'gasolina', name: 'Gasolina', icon: 'local_gas_station', color: 'text-slate-600', bgColor: 'bg-slate-100', borderColor: 'border-slate-200' },
                { id: 'transporte-publico', name: 'Transporte Público', icon: 'directions_bus', color: 'text-yellow-600', bgColor: 'bg-yellow-50', borderColor: 'border-yellow-100' },
            ]
        },
        {
            id: 'estilo-vida',
            name: 'Estilo de Vida',
            icon: 'style',
            color: 'purple',
            count: 0,
            monthly: '0',
            items: []
        }
    ];

    const colors = [
        '#307de8', '#ff6b6b', '#4ecdc4', '#ffd93d', '#6c5ce7', '#a8e6cf', '#e17055'
    ];

    const icons = [
        'shopping_cart', 'restaurant', 'directions_car', 'flight', 'home',
        'fitness_center', 'pets', 'school', 'videogame_asset'
    ];

    const toggleSelection = (id: string) => {
        setSelectedItems(prev => prev.includes(id) ? prev.filter(i => i !== id) : [...prev, id]);
    };

    return (
        <div className="flex h-full w-full overflow-hidden relative">
            {/* Background blobs */}
            <div className="absolute top-[-20%] right-[-10%] w-[600px] h-[600px] bg-blue-400/15 rounded-full blur-[100px] pointer-events-none"></div>
            <div className="absolute bottom-[-10%] left-[-10%] w-[500px] h-[500px] bg-purple-400/10 rounded-full blur-[100px] pointer-events-none"></div>

            {/* Main Content Area */}
            <div className="flex-1 flex flex-col h-full overflow-hidden z-10">
                <header className="px-8 py-6 flex items-center justify-between border-b border-white/40">
                    <div className="flex flex-col gap-1">
                        <div className="flex items-center gap-2 text-xs font-semibold text-slate-500 mb-1">
                            <span>Configuración</span>
                            <span className="material-symbols-outlined text-[14px]">chevron_right</span>
                            <span className="text-primary">Categorías</span>
                        </div>
                        <h2 className="text-2xl font-black text-slate-900 tracking-tight dark:text-white">Gestión de Categorías</h2>
                    </div>
                    <div className="flex items-center gap-4">
                        <button className="btn-interact size-10 flex items-center justify-center rounded-xl bg-white border border-slate-200 text-slate-500 shadow-sm dark:bg-slate-800 dark:border-slate-700">
                            <span className="material-symbols-outlined">search</span>
                        </button>
                        <div className="w-[1px] h-6 bg-slate-200 mx-1"></div>
                        <button
                            onClick={() => setIsModalOpen(true)}
                            className="btn-interact bg-primary hover:bg-primary-hover text-white px-5 py-2.5 rounded-xl flex items-center gap-2 shadow-lg shadow-blue-500/20 font-bold text-sm"
                        >
                            <span className="material-symbols-outlined text-[20px]">add</span>
                            <span>Nueva Categoría</span>
                        </button>
                    </div>
                </header>

                <div className="flex-1 overflow-y-auto p-8 scrollbar-hide pb-32">
                    <div className="max-w-5xl mx-auto space-y-8">
                        <div className="flex flex-col md:flex-row justify-between items-center gap-4">
                            <div className="flex bg-slate-200/50 dark:bg-slate-800 p-1 rounded-xl">
                                <button
                                    onClick={() => setSelectedTab('Gastos')}
                                    className={`px-6 py-2 rounded-lg text-sm font-bold transition-all ${selectedTab === 'Gastos' ? 'bg-white dark:bg-slate-700 text-slate-900 dark:text-white shadow-sm' : 'text-slate-500'}`}
                                >
                                    Gastos
                                </button>
                                <button
                                    onClick={() => setSelectedTab('Ingresos')}
                                    className={`px-6 py-2 rounded-lg text-sm font-bold transition-all ${selectedTab === 'Ingresos' ? 'bg-white dark:bg-slate-700 text-slate-900 dark:text-white shadow-sm' : 'text-slate-500'}`}
                                >
                                    Ingresos
                                </button>
                            </div>
                            <div className="flex items-center gap-3">
                                <div className="flex items-center gap-2 px-3 py-1.5 rounded-lg border border-slate-200 bg-white/50 dark:bg-slate-800/50 dark:border-slate-700 text-xs font-bold text-slate-500">
                                    <span className="material-symbols-outlined text-[18px]">folder</span>
                                    5 Grupos
                                </div>
                                <div className="flex items-center gap-2 px-3 py-1.5 rounded-lg border border-slate-200 bg-white/50 dark:bg-slate-800/50 dark:border-slate-700 text-xs font-bold text-slate-500">
                                    <span className="material-symbols-outlined text-[18px]">category</span>
                                    24 Categorías
                                </div>
                            </div>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                            {folders.map(folder => (
                                <div key={folder.id} className="glass-card rounded-[2rem] p-6 shadow-soft group/folder">
                                    <div className="flex items-center justify-between mb-4 pb-4 border-b border-slate-100 dark:border-slate-800">
                                        <div className="flex items-center gap-3">
                                            <span className="material-symbols-outlined text-slate-300 drag-handle">drag_indicator</span>
                                            <div className={`size-11 rounded-xl flex items-center justify-center ${folder.color === 'blue' ? 'bg-blue-100 text-blue-600' :
                                                    folder.color === 'emerald' ? 'bg-emerald-100 text-emerald-600' :
                                                        folder.color === 'indigo' ? 'bg-indigo-100 text-indigo-600' : 'bg-purple-100 text-purple-600'
                                                }`}>
                                                <span className="material-symbols-outlined text-2xl">{folder.icon}</span>
                                            </div>
                                            <div>
                                                <h4 className="font-black text-slate-900 dark:text-white text-lg leading-tight">{folder.name}</h4>
                                                <p className="text-[11px] font-bold text-slate-400">{folder.count} categorías • {folder.monthly}/mes</p>
                                            </div>
                                        </div>
                                        <div className="flex gap-1">
                                            <button className="size-8 rounded-lg hover:bg-slate-50 dark:hover:bg-slate-800 text-slate-400 flex items-center justify-center transition-colors">
                                                <span className="material-symbols-outlined text-[20px]">add</span>
                                            </button>
                                        </div>
                                    </div>

                                    {folder.items.length > 0 ? (
                                        <div className="space-y-1">
                                            {folder.items.map(item => {
                                                const isSelected = selectedItems.includes(item.id);
                                                return (
                                                    <div key={item.id} className={`flex items-center justify-between p-2 rounded-xl transition-all border group/row ${isSelected ? 'bg-primary/5 border-primary/20' : 'border-transparent hover:bg-white hover:border-slate-100 dark:hover:bg-slate-800 dark:hover:border-slate-700'
                                                        }`}>
                                                        <div className="flex items-center gap-3 flex-1">
                                                            <span className={`material-symbols-outlined text-[20px] text-slate-200 transition-opacity ${isSelected ? 'opacity-100 text-primary/40' : 'opacity-0 group-hover/row:opacity-100'}`}>drag_handle</span>
                                                            <input
                                                                type="checkbox"
                                                                checked={isSelected}
                                                                onChange={() => toggleSelection(item.id)}
                                                                className="rounded border-slate-300 text-primary focus:ring-primary/20 bg-transparent size-4 cursor-pointer"
                                                            />
                                                            <div className={`size-8 rounded-full flex items-center justify-center border ${item.bgColor} ${item.borderColor} dark:bg-slate-700 dark:border-slate-600`}>
                                                                <span className={`material-symbols-outlined text-[18px] ${item.color}`}>{item.icon}</span>
                                                            </div>
                                                            <span className={`text-sm font-bold ${isSelected ? 'text-slate-900 dark:text-white' : 'text-slate-600 dark:text-slate-400'}`}>{item.name}</span>
                                                        </div>
                                                        <button className={`p-1.5 rounded-lg hover:bg-slate-100 dark:hover:bg-slate-700 text-slate-400 transition-opacity ${isSelected ? 'opacity-100 text-primary' : 'opacity-0 group-hover/row:opacity-100'}`}>
                                                            <span className="material-symbols-outlined text-[18px]">edit</span>
                                                        </button>
                                                    </div>
                                                );
                                            })}
                                        </div>
                                    ) : (
                                        <div className="border-2 border-dashed border-slate-200 dark:border-slate-800 rounded-2xl p-8 flex flex-col items-center justify-center bg-slate-50/50 dark:bg-slate-900/20">
                                            <p className="text-sm font-bold text-slate-400">Arrastra categorías aquí</p>
                                            <p className="text-[11px] font-bold text-slate-300">o crea una nueva</p>
                                        </div>
                                    )}
                                </div>
                            ))}
                        </div>
                    </div>
                </div>

                {/* Selection Bar */}
                {selectedItems.length > 0 && (
                    <div className="absolute bottom-6 left-1/2 -translate-x-1/2 z-50 animate-slide-up">
                        <div className="flex items-center gap-4 bg-slate-900 text-white px-6 py-3 rounded-2xl shadow-premium backdrop-blur-md bg-opacity-95 border border-slate-800 transition-all">
                            <div className="flex items-center gap-3 pr-4 border-r border-slate-700">
                                <span className="bg-primary text-white text-[10px] font-black px-2 py-0.5 rounded-full">{selectedItems.length}</span>
                                <span className="text-sm font-bold">Seleccionados</span>
                            </div>
                            <div className="flex items-center gap-2">
                                <button className="flex items-center gap-2 px-3 py-1.5 rounded-lg hover:bg-white/10 transition-colors">
                                    <span className="material-symbols-outlined text-[20px]">drive_file_move</span>
                                    <span className="text-sm font-medium">Mover</span>
                                </button>
                                <button className="flex items-center gap-2 px-3 py-1.5 rounded-lg hover:bg-red-500/20 text-red-400 transition-colors">
                                    <span className="material-symbols-outlined text-[20px]">delete</span>
                                    <span className="text-sm font-medium">Eliminar</span>
                                </button>
                            </div>
                            <button onClick={() => setSelectedItems([])} className="ml-2 text-slate-500 hover:text-white transition-colors">
                                <span className="material-symbols-outlined">close</span>
                            </button>
                        </div>
                    </div>
                )}
            </div>

            {/* NEW CATEGORY MODAL */}
            {isModalOpen && (
                <div className="fixed inset-0 z-[100] flex items-center justify-center p-4 sm:p-8 bg-slate-900/40 backdrop-blur-sm animate-fade-in">
                    <div className="glass-card w-full max-w-2xl rounded-2xl shadow-premium overflow-hidden flex flex-col animate-slide-up border border-white/80 dark:border-slate-800">
                        {/* Modal Header */}
                        <div className="flex items-center justify-between px-8 py-6 border-b border-gray-100 dark:border-gray-800">
                            <div>
                                <h1 className="text-2xl font-black tracking-tight text-gray-900 dark:text-white uppercase tracking-widest">Nueva Categoría</h1>
                                <p className="text-sm text-gray-500 dark:text-gray-400 mt-1 font-medium">Organiza tus transacciones con claridad.</p>
                            </div>
                            <button
                                onClick={() => setIsModalOpen(false)}
                                className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-200 transition-colors p-2 rounded-full hover:bg-black/5 dark:hover:bg-white/5 group"
                            >
                                <span className="material-symbols-outlined text-2xl group-hover:rotate-90 transition-transform duration-300">close</span>
                            </button>
                        </div>

                        {/* Modal Body */}
                        <div className="p-8 space-y-8 overflow-y-auto max-h-[70vh] scrollbar-hide">
                            {/* Category Name */}
                            <div className="space-y-3">
                                <label className="block text-xs font-black uppercase tracking-widest text-gray-700 dark:text-gray-300 ml-1" htmlFor="category-name">
                                    Nombre de la categoría
                                </label>
                                <div className="relative">
                                    <input
                                        value={newCatName}
                                        onChange={(e) => setNewCatName(e.target.value)}
                                        className="w-full h-14 pl-6 pr-6 rounded-xl border border-gray-200 dark:border-gray-700 bg-white/50 dark:bg-slate-800/50 focus:bg-white dark:focus:bg-slate-800 focus:ring-4 focus:ring-primary/10 focus:border-primary text-gray-900 dark:text-white placeholder-gray-400 transition-all outline-none text-base font-bold shadow-sm"
                                        id="category-name"
                                        placeholder="Ej. Alimentación, Transporte, Ocio..."
                                        type="text"
                                    />
                                </div>
                            </div>

                            {/* Icon & Color Picker */}
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                                {/* Color Picker */}
                                <div className="space-y-4">
                                    <label className="block text-xs font-black uppercase tracking-widest text-gray-700 dark:text-gray-300 ml-1">
                                        Color identificativo
                                    </label>
                                    <div className="flex flex-wrap gap-3">
                                        {colors.map(color => (
                                            <button
                                                key={color}
                                                onClick={() => setSelectedColor(color)}
                                                className={`w-10 h-10 rounded-full transition-all hover:scale-110 active:scale-90 ${selectedColor === color ? 'ring-2 ring-offset-2 ring-primary dark:ring-offset-slate-900' : ''}`}
                                                style={{ backgroundColor: color }}
                                            />
                                        ))}
                                        <button className="w-10 h-10 rounded-full border-2 border-dashed border-gray-300 dark:border-gray-600 flex items-center justify-center text-gray-400 hover:text-primary hover:border-primary transition-colors">
                                            <span className="material-symbols-outlined text-xl">add</span>
                                        </button>
                                    </div>
                                </div>

                                {/* Icon Picker */}
                                <div className="space-y-4">
                                    <label className="block text-xs font-black uppercase tracking-widest text-gray-700 dark:text-gray-300 ml-1">
                                        Icono
                                    </label>
                                    <div className="grid grid-cols-5 gap-2">
                                        {icons.map(icon => (
                                            <button
                                                key={icon}
                                                onClick={() => setSelectedIcon(icon)}
                                                className={`w-10 h-10 rounded-lg flex items-center justify-center transition-all ${selectedIcon === icon
                                                        ? 'bg-primary text-white shadow-lg shadow-primary/25 scale-105'
                                                        : 'text-gray-400 bg-gray-50 dark:bg-white/5 border border-transparent hover:bg-gray-100 dark:hover:bg-white/10'
                                                    }`}
                                            >
                                                <span className="material-symbols-outlined text-[20px]">{icon}</span>
                                            </button>
                                        ))}
                                        <button className="w-10 h-10 rounded-lg border border-dashed border-gray-300 dark:border-gray-600 flex items-center justify-center text-gray-400 hover:text-primary hover:border-primary transition-all">
                                            <span className="material-symbols-outlined text-xl">search</span>
                                        </button>
                                    </div>
                                </div>
                            </div>

                            {/* Folder Association */}
                            <div className="space-y-4">
                                <div className="flex items-center justify-between px-1">
                                    <label className="text-xs font-black uppercase tracking-widest text-gray-700 dark:text-gray-300" htmlFor="folder-select">
                                        Carpeta asociada
                                    </label>
                                    <button className="text-[10px] font-black uppercase tracking-widest text-primary hover:text-primary-hover transition-colors flex items-center gap-1.5">
                                        <span className="material-symbols-outlined text-[16px]">create_new_folder</span>
                                        Nueva carpeta
                                    </button>
                                </div>
                                <div className="relative group">
                                    <span className="absolute left-4 top-1/2 -translate-y-1/2 text-slate-400 pointer-events-none">
                                        <span className="material-symbols-outlined">folder_open</span>
                                    </span>
                                    <select
                                        value={selectedFolder}
                                        onChange={(e) => setSelectedFolder(e.target.value)}
                                        className="w-full h-14 pl-12 pr-10 rounded-xl border border-gray-200 dark:border-gray-700 bg-white/50 dark:bg-slate-800/50 focus:bg-white dark:focus:bg-slate-800 focus:ring-4 focus:ring-primary/10 focus:border-primary text-gray-900 dark:text-white transition-all outline-none appearance-none cursor-pointer font-bold shadow-sm"
                                        id="folder-select"
                                    >
                                        <option value="none">Sin carpeta (Categoría raíz)</option>
                                        <option value="gastos">Gastos Mensuales</option>
                                        <option value="ahorro">Ahorro e Inversión</option>
                                        <option value="ocio">Ocio y Entretenimiento</option>
                                        <option value="hogar">Hogar y Servicios</option>
                                    </select>
                                    <span className="absolute right-4 top-1/2 -translate-y-1/2 text-slate-400 pointer-events-none group-hover:text-primary transition-colors">
                                        <span className="material-symbols-outlined">expand_more</span>
                                    </span>
                                </div>
                                <div className="flex items-start gap-2 bg-blue-50 dark:bg-blue-900/20 p-3 rounded-xl border border-blue-100 dark:border-blue-800/30">
                                    <span className="material-symbols-outlined text-primary text-[18px] shrink-0">info</span>
                                    <p className="text-[11px] text-blue-700 dark:text-blue-300 font-bold leading-relaxed">
                                        Asociar a una carpeta ayuda a agrupar los gastos en los reportes mensuales y permite definir presupuestos grupales automáticos.
                                    </p>
                                </div>
                            </div>

                            {/* Preview Card */}
                            <div className="bg-gray-50 dark:bg-slate-900/30 rounded-2xl p-5 flex items-center gap-5 border border-gray-100 dark:border-slate-800 animate-fade-in shadow-inner">
                                <div
                                    className="w-14 h-14 rounded-2xl flex items-center justify-center text-white shadow-xl transition-all duration-500 scale-110"
                                    style={{ backgroundColor: selectedColor }}
                                >
                                    <span className="material-symbols-outlined text-[28px]">{selectedIcon}</span>
                                </div>
                                <div>
                                    <p className="text-[10px] uppercase tracking-[0.2em] text-gray-400 font-black mb-1">Vista previa</p>
                                    <p className="text-lg font-black text-gray-900 dark:text-white leading-tight">{newCatName || 'Nombre de la categoría'}</p>
                                    <p className="text-[11px] font-bold text-gray-500 dark:text-gray-400 mt-0.5">Dentro de "{selectedFolder === 'none' ? 'Raíz' : selectedFolder.charAt(0).toUpperCase() + selectedFolder.slice(1)}"</p>
                                </div>
                            </div>
                        </div>

                        {/* Modal Footer Actions */}
                        <div className="px-8 py-6 border-t border-gray-100 dark:border-gray-800 bg-white/40 dark:bg-slate-900/40 flex items-center justify-end gap-4 backdrop-blur-md">
                            <button
                                onClick={() => setIsModalOpen(false)}
                                className="px-6 h-12 rounded-xl text-gray-600 dark:text-gray-300 font-black text-xs uppercase tracking-widest hover:bg-gray-100 dark:hover:bg-slate-800 transition-all"
                            >
                                Cancelar
                            </button>
                            <button
                                onClick={() => setIsModalOpen(false)}
                                className="px-8 h-12 rounded-xl bg-primary hover:bg-primary-hover text-white font-black text-xs uppercase tracking-[0.2em] shadow-lg shadow-primary/25 hover:shadow-primary/40 active:scale-95 transition-all flex items-center gap-2.5"
                            >
                                <span className="material-symbols-outlined text-[20px]">check</span>
                                Guardar
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
